.global-loading{position:fixed;top:50%;left:50%;-webkit-transform:translate(-50%,-50%);transform:translate(-50%,-50%);z-index:10000;border-radius:8px;display:-webkit-box;display:-ms-flexbox;display:flex;-webkit-box-align:center;-ms-flex-align:center;align-items:center;-webkit-box-pack:center;-ms-flex-pack:center;justify-content:center;pointer-events:none;-webkit-box-shadow:0 12px 30px rgba(0,0,0,.16);box-shadow:0 12px 30px rgba(0,0,0,.16);width:100px;height:100px}.global-loading.light{background-color:#fff}.global-loading.dark{background-color:#202735}.global-loading>.global-loading-img{width:48px;height:48px}