.dsm-upgrade-tooltip{position:relative;display:inline-block;cursor:pointer;line-height:1.15;padding:5px 15px 5px 0;margin-left:5px;word-break:break-word}.dsm-upgrade-tooltip .tips-content{position:absolute;white-space:normal;background-color:#fff;line-height:24px;box-shadow:0 2px 8px 0 rgba(0,0,0,.15);z-index:1000;display:none;left:40px;top:-20px;width:300px;height:fit-content;padding:8px 11px;text-align:left;border-radius:5px;box-shadow:1px 1px 8px 0 rgba(0,0,0,.16)}.dsm-upgrade-tooltip .tips-content.right{right:0;transform:translateX(0)}.dsm-upgrade-tooltip .tips-content:before{border-right:10px solid rgba(0,0,0,.1);left:-11px}.dsm-upgrade-tooltip .tips-content:after,.dsm-upgrade-tooltip .tips-content:before{content:"";width:0;height:0;border-top:10px solid transparent;border-bottom:10px solid transparent;position:absolute;top:20px}.dsm-upgrade-tooltip .tips-content:after{border-right:10px solid #fff;left:-10px}.dsm-upgrade-tooltip .tips-content .go-buy-link{word-break:keep-all}.dsm-upgrade-tooltip:hover .tips-content{display:block;color:#2e2f30}