.dsm-c-panel{background-color:#fff;box-shadow:0 3px 10px 0 rgba(0,0,0,.15);width:90%;max-width:960px;min-width:650px;margin:auto;box-sizing:border-box;border-radius:6px}.dsm-c-panel .dsm-c-panel-title{width:100%;padding:0 30px;height:70px;line-height:70px;border-bottom:1px solid #dcddde;box-sizing:border-box}.dsm-c-panel .dsm-c-panel-title .panel-title-left{line-height:normal;display:flex;align-items:center;height:70px}.dsm-c-panel .dsm-c-panel-title .back-link{margin-right:6px;outline:0;width:20px;height:20px;display:inline-flex;align-items:center;justify-content:center;text-decoration:none}.dsm-c-panel .dsm-c-panel-title .back-link:hover .dsm-c-icon{color:#fe4066}.dsm-c-panel .dsm-c-panel-title h5{display:inline-block;font-size:1.8rem;font-weight:400}.dsm-c-panel .dsm-c-panel-title .edit-title{float:left;display:flex;align-items:center;height:100%}.dsm-c-panel .dsm-c-panel-title .actions{float:right;color:#fe4066}.dsm-c-panel .dsm-c-panel-title .actions span{margin-left:20px;cursor:pointer}.dsm-c-panel .dsm-c-panel-content{padding:30px}