.rp-free-record{margin-top:8px}.rp-free-record .record-table{table-layout:fixed;width:100%}.rp-free-record .record-table thead{border-bottom:1px solid #dcddde}.rp-free-record .record-table thead td{line-height:36px;padding-bottom:4px}.rp-free-record .record-table tbody td{line-height:40px}.rp-free-record .record-table tbody td.reward{color:#fe4066}.rp-free-record .record-table td{text-align:center;height:20px;font-size:12px;color:#81868b;overflow:hidden;white-space:nowrap;text-overflow:ellipsis;padding:0 4px}.rp-free-record .empty{display:flex;align-items:center;flex-direction:column;margin-top:48px}.rp-free-record .empty img{width:160px;height:72px}.rp-free-record .empty-tip{margin-top:24px;font-size:12px;color:#969798;line-height:20px}