.mobile-dialog{width:100%;height:100%;position:fixed;top:0;left:0;background:rgba(0,0,0,.5);z-index:20}.mobile-dialog .mobile-loading{position:absolute;top:50%;left:50%;transform:translate(-50%,-50%)}.mobile-dialog .mobile-loading img{display:block}.mobile-dialog .mobile-content{min-height:350px;background:#fff;border-radius:16px;position:fixed;width:86%;left:7%;top:50%;transform:translateY(-50%);text-align:center;overflow:hidden;margin:0 auto}.mobile-dialog .mobile-content>img{max-width:100%;display:block}.mobile-dialog .mobile-content .contents{padding:0 10px 25px;position:relative}.mobile-dialog .mobile-content .title{font-size:18px;color:#2e2f30;margin:24px 0 15px;font-weight:700}.mobile-dialog .mobile-content .text,.mobile-dialog .mobile-content .text p{font-size:14px;color:#696b6c;line-height:150%}.mobile-dialog .mobile-content .buttons{margin:20px 0}.mobile-dialog .mobile-content .buttons button{margin:0 8px}.mobile-dialog .mobile-content .other{color:#696b6c;font-size:14px}.mobile-dialog .mobile-content .other .red{color:#fe4066}.mobile-dialog.mobile-dialog-fail .mobile-content{padding:20px 0 10px;border-radius:8px;min-height:0}.mobile-dialog.mobile-dialog-fail .mobile-content .text{text-align:center;display:inline-block;padding:0 10px}.mobile-dialog.mobile-dialog-fail .mobile-content .text p{text-align:left;position:relative;padding-left:8px}.mobile-dialog.mobile-dialog-fail .mobile-content .text p:before{content:"";width:3px;height:3px;border-radius:100%;background:#969798;position:absolute;left:0;top:8.5px}.mobile-dialog.mobile-dialog-fail .mobile-content .buttons{margin-top:30px}@media (max-width:359px){.mobile-dialog .mobile-content{width:90%;left:5%}.mobile-dialog .mobile-content .contents .title{font-size:16px}.mobile-dialog .mobile-content .contents .text{font-size:12px}}