.top-message-tips-wrap{min-width:480px;max-width:80%;position:fixed;left:50%;top:32px;-webkit-transform:translateX(-50%);-moz-transform:translateX(-50%);-ms-transform:translateX(-50%);-o-transform:translateX(-50%);transform:translateX(-50%);z-index:3001}.top-message-tips-wrap>div{position:relative;background:#fff;border-radius:6px;box-shadow:0 5px 30px 0 rgba(0,0,0,.15);padding:14px 130px 14px 32px;line-height:24px}.top-message-tips-wrap>div .text{padding:2px 0}.top-message-tips-wrap>div .text i{padding:0 1px 0 5px;font-style:normal;color:#fe4066;font-weight:600}.top-message-tips-wrap>div .link-b{position:absolute;right:32px;min-width:48px;text-align:center;line-height:28px;display:block;padding:0 10px;border-radius:4px;top:14px;transition:all .2s}.top-message-tips-wrap>div .link-b:hover{background:#ebedee;text-decoration:none}.top-message-tips-wrap .close_btn{position:absolute;right:7px;top:6px}