.dsm-c-slider,.dsm-c-slider-click{display:inline-block}.dsm-c-slider-click{height:16px;position:absolute;left:0;top:-6px}.dsm-c-sliding-block{height:2px;background-color:#dcddde;border-radius:2px;position:relative;display:inline-block;vertical-align:middle}.dsm-c-sliding-block .dsm-c-block{height:100%;background-color:#696b6c;border-radius:2px;position:relative;pointer-events:none}.dsm-c-sliding-block .dsm-c-block-btn{display:inline-block;width:12px;height:12px;border:1px solid #dcddde;background-color:#fff;border-radius:50%;box-shadow:0 1px 1px rgba(46,47,48,.15);position:absolute;top:-5px;right:-7px;pointer-events:auto}.dsm-c-block-title{width:30px;text-align:center;background-color:#fff;font-size:12px;display:inline-block;margin:0 10px;user-select:none}.disabled-slider-title{opacity:.25}.disabled-slider-block{background-color:#dcddde!important}