.dsm-c-tooltip{position:relative;display:inline-block;cursor:default;line-height:1.15}.dsm-c-tooltip .tips{position:absolute;white-space:nowrap;text-align:center;background-color:#fff;padding:0 20px;height:36px;line-height:36px;box-shadow:0 2px 8px 0 rgba(0,0,0,.15);z-index:1000;display:none}.dsm-c-tooltip .tips.size-s{padding:0 10px;line-height:24px;height:24px;font-size:1.2rem;box-shadow:0 2px 2px 2px rgba(0,0,0,.1)}.dsm-c-tooltip .tips.down{bottom:-44px}.dsm-c-tooltip .tips.down.size-s{bottom:-34px}.dsm-c-tooltip .tips.up{top:-44px}.dsm-c-tooltip .tips.up.size-s{top:-34px}.dsm-c-tooltip .tips.center{left:50%;transform:translateX(-50%)}.dsm-c-tooltip .tips.left{left:0;transform:translateX(0)}.dsm-c-tooltip .tips.right{right:0;transform:translateX(0)}.dsm-c-tooltip:hover .tips{display:block;color:#2e2f30}