.dsm-c-search-input{height:36px;position:relative}.dsm-c-search-input input{box-sizing:border-box;width:100%;height:100%;color:#2e2f30;border:0;border-bottom:1px solid #b8bbbf;outline:none;padding:0 28px;transition:border-color .2s}.dsm-c-search-input input::-webkit-input-placeholder{color:#b8bbbf}.dsm-c-search-input input::-moz-placeholder{color:#b8bbbf}.dsm-c-search-input input:-ms-input-placeholder{color:#b8bbbf}.dsm-c-search-input input:hover{border-color:#969798}.dsm-c-search-input input:focus{border-color:#418fd7}.dsm-c-search-input .dsm-c-disabled{cursor:default;color:#dcddde;background-color:inherit;pointer-events:none}.dsm-c-search-input .dsm-c-disabled:focus{border-color:#aaa!important}.dsm-c-search-input .dsm-c-icon{position:absolute;line-height:normal;top:10px}.dsm-c-search-input .icon-_search{left:4px;cursor:default}.dsm-c-search-input .icon-_close{right:4px}.dsm-c-search-input-full-border input{border:1px solid #b8bbbf;border-radius:4px;padding-left:36px}.dsm-c-search-input-full-border .icon-_search{left:12px}.dsm-c-search-input-full-border .icon-_close{right:8px}.dsm-c-search-input-inline-box{width:100%}.dsm-c-search-input-inline-box input{border-bottom:1px solid #dcddde;padding:0 36px}.dsm-c-search-input-inline-box .icon-_search{left:12px}.dsm-c-search-input-inline-box .icon-_close{right:10px}