.pure-text-editor{position:fixed;z-index:1000;background-color:#fff;font-size:22.4px!important;font-size:1.4rem!important}.pure-text-editor .dsm-c-rp-input{border:none!important}.pure-text-editor .dsm-c-rp-input>input[type=text]{width:100%;border:1px solid #009dff!important;text-indent:6px!important;outline:none!important;color:#181f29!important;font-size:inherit!important}.pure-text-editor>.general-input input{position:absolute;max-width:10px;width:calc(100% - 14px)}.pure-text-editor>.general-multi-editor{width:100%;height:100%;-webkit-box-sizing:border-box;box-sizing:border-box;border:1px solid #ddd;-webkit-box-shadow:0 6px 12px rgba(0,0,0,.15);box-shadow:0 6px 12px rgba(0,0,0,.15)}.pure-text-editor>.general-multi-editor .multi-editor{width:calc(100% - 15px);height:auto;position:relative}.pure-text-editor>.general-multi-editor .multi-editor:first-child{padding-bottom:18px}.pure-text-editor>.general-multi-editor .multi-editor>*{line-height:18px;white-space:pre-wrap;word-break:break-all;font-family:Microsoft YaHei;width:100%;overflow:hidden;padding:2px;outline:none;resize:none;border:none}.pure-text-editor>.general-multi-editor .multi-editor>textarea{position:absolute;top:0}