.dsm-c-message{position:fixed;top:80px;min-width:300px;max-width:660px;padding:30px 50px;text-align:center;background-color:#fff;box-shadow:0 3px 10px 0 rgba(0,0,0,.15);left:50%;transform:translateX(-50%);z-index:4000}.dsm-c-message .dsm-c-icon{cursor:unset}.dsm-c-message .text{margin-top:20px;line-height:1.15;font-size:14px;user-select:text}.dsm-c-message .info-text{width:16px;height:16px;text-align:center;line-height:16px;color:#fff;border-radius:50%;background-color:#65d505;display:inline-block}