.dsm-c-rp-progressbar{height:32px;position:relative;width:calc(100% - 40px);margin:0 auto}.dsm-c-rp-progressbar>.progress-track{position:absolute;width:100%;height:2px;background-color:#f0f0f0;border-radius:2px;bottom:0}.dsm-c-rp-progressbar>.progress-track>.progress-track-rate{background-color:#fe4066;height:100%;border-radius:2px;position:absolute}.dsm-c-rp-progressbar>.progress-tips{position:absolute;bottom:12px;width:40px;margin-left:-20px;height:20px;font-style:normal;font-size:19.2px;font-size:1.2rem;color:#fff;line-height:20px;text-align:center;background-color:#fe4066;border-radius:4px;-webkit-user-select:none;-moz-user-select:none;-ms-user-select:none;user-select:none}.dsm-c-rp-progressbar>.progress-tips:after{content:"";position:absolute;bottom:-8px;left:50%;margin-left:-4px;border:4px solid transparent;border-top-color:#fe4066}