.dsm-c-drop{position:absolute;min-width:120px;margin:0;padding:10px 0;background-color:#fff;box-shadow:0 4px 12px 0 rgba(0,0,0,.15);text-align:left;user-select:none;z-index:2000}.dsm-c-drop li{line-height:30px;text-overflow:ellipsis;white-space:pre}.dsm-c-drop li.has-icon{display:flex;padding:0 20px}.dsm-c-drop li.has-icon:hover{background-color:#f0f0f0;cursor:pointer}.dsm-c-drop li.has-icon>.dsm-c-icon{margin-right:10px}.dsm-c-drop li.has-icon .menu-item{padding-left:0}.dsm-c-drop li .menu-item{display:inline-block;box-sizing:border-box;width:100%;padding:0 0 0 20px;text-decoration:none;color:#2e2f30}.dsm-c-drop li .menu-item:hover{background-color:#f0f0f0;cursor:pointer}.dsm-c-drop li .menu-item .dsm-upgrade-tooltip{margin:0 0 0 5px!important}.dsm-c-drop li .menu-item .tips{display:inline-block;background-color:#fe4066;color:#fff;font-style:normal;font-size:1.2rem;padding:0 8px;margin-left:10px;border-radius:4px;width:auto!important;height:auto!important}.dsm-c-drop li .menu-item .tipText{font-style:normal;font-size:1.2rem;margin-left:30px}.dsm-c-drop li .menu-item-title.menu-item-title-badge{display:flex;align-items:center}.dsm-c-drop li .menu-item-title .badge-dot{height:100%;position:relative;margin-left:4px}.dsm-c-drop li .menu-item-title .badge-dot .dot{display:inline-block;width:5px;height:5px;background:#fe4066;border-radius:50%;position:absolute;top:0}.dsm-c-drop li .menu-item-title .badge-count{display:inline-block;margin-left:4px;background:#fe4066;color:#fff;min-width:16px;height:16px;line-height:16px;width:fit-content;text-align:center;border-radius:10px;font-size:10px}.dsm-c-drop li .select-item{background-color:#f0f0f0;cursor:pointer}.dsm-c-drop li.split-line{display:block;height:1px;width:100%;background-color:#dcddde;margin:4px 0}.dsm-c-drop .dsm-c-disabled{cursor:unset!important}.dsm-c-drop .dsm-c-disabled>.menu-item-title,.dsm-c-drop .dsm-c-disabled>p{opacity:.25}.dsm-c-drop .dsm-c-disabled:hover{background-color:#fff!important;cursor:unset!important}.dsm-c-drop .dsm-c-disabled .dsm-upgrade-tooltip{font-size:1.4rem!important}.dsm-c-drop .dsm-c-disabled .dsm-upgrade-tooltip p{font-size:1.4rem!important;color:#2e2f30!important}