.dsm-c-rp-color-plate{width:100%;height:152px;position:relative;-webkit-user-select:none;-moz-user-select:none;-ms-user-select:none;user-select:none}.dsm-c-rp-color-plate>*{position:absolute;width:100%;height:100%;top:0;left:0;pointer-events:none}.dsm-c-rp-color-plate>:nth-child(2){background:-webkit-gradient(linear,left top,right top,from(#fff),to(hsla(0,0%,100%,0)));background:linear-gradient(90deg,#fff,hsla(0,0%,100%,0))}.dsm-c-rp-color-plate>:nth-child(3){background:-webkit-gradient(linear,left bottom,left top,from(#000),to(transparent));background:linear-gradient(0deg,#000,transparent)}.dsm-c-rp-color-plate>:last-child{pointer-events:all;width:10px;height:10px;border:2px solid #fff;-webkit-box-sizing:border-box;box-sizing:border-box;-webkit-box-shadow:0 0 2px 2px rgba(0,0,0,.2);box-shadow:0 0 2px 2px rgba(0,0,0,.2);border-radius:50%;-webkit-transform:translate(-50%,-50%);transform:translate(-50%,-50%);-webkit-user-select:none;-moz-user-select:none;-ms-user-select:none;user-select:none}