.dsm-c-rp-switch{position:relative;width:60px;height:30px;display:inline-block;-webkit-user-select:none;-moz-user-select:none;-ms-user-select:none;user-select:none;cursor:pointer;-webkit-transition-duration:.1s;transition-duration:.1s;-webkit-transition-timing-function:ease-in;transition-timing-function:ease-in;-webkit-transition-property:background-color;transition-property:background-color}.dsm-c-rp-switch>i{position:absolute;top:2px;background:#eef0f4;width:26px;height:26px;-webkit-transition-duration:.1s;transition-duration:.1s;-webkit-transition-timing-function:ease-in;transition-timing-function:ease-in;-webkit-transition-property:all;transition-property:all}.dsm-c-rp-switch.on{background-color:#009dff}.dsm-c-rp-switch.off{background-color:#42516c}.dsm-c-rp-switch.off>i{opacity:.7}.dsm-c-rp-switch.undefined{background-color:#696b6c}.dsm-c-rp-switch.disabled{cursor:default;pointer-events:none;opacity:.3}