.dsm-c-progress{height:26px;position:relative}.dsm-c-progress .track{width:100%;height:2px;background-color:#f0f0f0;border-radius:2px;position:absolute;bottom:0}.dsm-c-progress .track .progress{background-color:#fe4066;height:100%;border-radius:2px;position:absolute}.dsm-c-progress .tips{position:absolute;bottom:10px;width:40px;margin-left:-20px;height:20px;font-style:normal;font-size:1.2rem;color:#fff;text-align:center;background-color:#fe4066;border-radius:4px;user-select:none}.dsm-c-progress .tips:after{content:"";position:absolute;bottom:-8px;left:50%;margin-left:-4px;border:4px solid transparent;border-top-color:#fe4066}