.dsm-c-scrollview{position:relative}.dsm-c-scrollview.disabled>:first-child{overflow:hidden!important;margin-right:0!important;margin-bottom:0!important}.dsm-c-scrollview.disabled:hover .track-bar{opacity:0!important}.dsm-c-scrollview>*{z-index:1}.dsm-c-scrollview>:first-child{z-index:0}.dsm-c-scrollview:hover .track-bar{opacity:1!important}.dsm-c-scrollview .track-bar>div{background-color:rgba(46,47,48,.2)!important;border-radius:10px!important}.dsm-c-scrollview .track-bar>div:hover{background-color:rgba(46,47,48,.4)!important}.dsm-c-scrollview .h-track{height:6px!important;width:100%;bottom:0;left:0;-webkit-transform-style:preserve-3d}.dsm-c-scrollview .v-track{width:6px!important;height:100%;top:0;right:0;-webkit-transform-style:preserve-3d}