.dsm-c-arrow-point-tip{position:relative;display:inline-block;cursor:default;line-height:1.15;color:#2e2f30}.dsm-c-arrow-point-tip .arrow-point{width:10px;height:10px;background:#fff;bottom:-10px;position:absolute;transform:rotate(45deg);left:15px}.dsm-c-arrow-point-tip .arrow-point-tips{position:absolute;white-space:nowrap;text-align:center;background-color:#fff;padding:0 20px;height:36px;line-height:36px;box-shadow:0 8px 8px 0 rgba(0,0,0,.16);z-index:1000;border-radius:4px}.dsm-c-arrow-point-tip .arrow-point-tips.size-s{padding:0 10px;line-height:24px;height:24px;font-size:1.2rem;box-shadow:0 2px 2px 2px rgba(0,0,0,.1)}.dsm-c-arrow-point-tip .arrow-point-tips.down{bottom:-40px}.dsm-c-arrow-point-tip .arrow-point-tips.down.size-s{bottom:-44px}.dsm-c-arrow-point-tip .arrow-point-tips.up{top:-40px}.dsm-c-arrow-point-tip .arrow-point-tips.up.size-s{top:-44px}.dsm-c-arrow-point-tip .arrow-point-tips.center{left:50%;transform:translateX(-50%)}.dsm-c-arrow-point-tip .arrow-point-tips.left{left:0;transform:translateX(0)}.dsm-c-arrow-point-tip .arrow-point-tips.right{right:0;transform:translateX(0)}