.dsm-c-option-button,.tag-button-group-setting{display:inline-block;position:relative;padding:0 20px;margin:0 10px 10px 0;line-height:30px;border-radius:100px;background-color:#f0f0f0;transition:all .2s ease;cursor:pointer;user-select:none;vertical-align:middle}.dsm-c-option-button:hover i,.tag-button-group-setting:hover i{top:-8px;opacity:1}.dsm-c-option-button.option-selected,.tag-button-group-setting.option-selected{background-color:#696b6c;cursor:default}.dsm-c-option-button.option-selected .dsm-c-icon,.dsm-c-option-button.option-selected .tag-text,.tag-button-group-setting.option-selected .dsm-c-icon,.tag-button-group-setting.option-selected .tag-text{color:#fff;cursor:default}.dsm-c-option-button.option-selected .tag-img,.tag-button-group-setting.option-selected .tag-img{margin-left:10px;cursor:pointer;vertical-align:middle}