.dsm-c-rp-pagination{display:-webkit-box;display:-ms-flexbox;display:flex;height:32px}.dsm-c-rp-pagination .button{color:#eef0f4;border:1px solid #323e52;border-radius:2px;text-align:center;height:100%;width:32px;line-height:32px;margin-right:8px;-webkit-box-sizing:border-box;box-sizing:border-box}.dsm-c-rp-pagination .button:hover{background-color:#283142}.dsm-c-rp-pagination .button:last-child{margin-right:0}.dsm-c-rp-pagination .button>.next-button .dsm-c-rp-icon{-webkit-transform:rotate(180deg);transform:rotate(180deg)}.dsm-c-rp-pagination .button.pagination-number.selected,.dsm-c-rp-pagination .button.pagination-number.selected:hover{background-color:#009dff}.dsm-c-rp-pagination .button.hidden-number{border:0;pointer-events:none}.dsm-c-rp-pagination .button.custom-next-button{width:-webkit-fit-content!important;width:-moz-fit-content!important;width:fit-content!important;white-space:nowrap;padding:0 8px}