.dsm-c-rp-popup-panel{position:fixed;color:#eef0f4;-webkit-box-shadow:0 1px 4px rgba(0,0,0,.16);box-shadow:0 1px 4px rgba(0,0,0,.16);background:#323e52;padding:10px 0;border-radius:4px;min-width:160px;z-index:2000;display:-webkit-box;display:-ms-flexbox;display:flex;-webkit-box-orient:vertical;-webkit-box-direction:normal;-ms-flex-direction:column;flex-direction:column;font-size:13px;border:1px solid #42516c}.dsm-c-rp-popup-panel.in-popup{z-index:4000}.dsm-c-rp-popup-panel.animation{-webkit-transition-duration:.15s;transition-duration:.15s;-webkit-transition-timing-function:ease-in;transition-timing-function:ease-in;-webkit-transition-property:opacity;transition-property:opacity}.dsm-c-rp-popup-panel.light{background-color:#fff!important;color:#2e2f30;border:none}