.dsm-c-zoomer{font-size:14px;height:20px;line-height:20px;position:relative;display:inline-block}.dsm-c-zoomer .zoom-btn{width:16px;height:16px;line-height:16px;text-align:center;display:inline-block;cursor:pointer;box-sizing:border-box}.dsm-c-zoomer .zoom-btn>:first-child{display:block;color:#fff}.dsm-c-zoomer .zoom-btn>:last-child{display:none;color:hsla(0,0%,100%,.5)}.dsm-c-zoomer .zoom-btn:hover>:first-child{display:none}.dsm-c-zoomer .zoom-btn:hover>:last-child{display:block}.dsm-c-zoomer.disableL .zoom-btn:first-child,.dsm-c-zoomer.disableR .zoom-btn:last-child{pointer-events:none;opacity:.3}.dsm-c-zoomer span{margin:0 6px;display:inline-block;width:38px;text-align:center;position:relative;color:#fff}.dsm-c-zoomer span>.dsm-c-input{position:absolute;left:0;top:0;height:18px}.dsm-c-zoomer span>.dsm-c-input>input{text-align:center;line-height:18px}.dsm-c-zoomer span.editing{color:transparent}.dsm-c-zoomer .no-editing{cursor:pointer}.dsm-c-zoomer .no-editing:hover{opacity:.8}.dsm-c-zoomer .no-editing:after{content:"%"}.dsm-c-zoomer .menu-item:after{content:""}.dsm-c-zoomer>.dsm-c-drop{min-width:100%;margin-top:4px}.dsm-c-zoomer>.dsm-c-drop span{text-align:left;margin:0}.dsm-c-zoomer>.dsm-c-drop span.menu-item{padding:0 20px}