.dsm-c-rp-badge{position:relative;display:inline-block}.dsm-c-rp-badge .dsm-c-rp-badge-item{position:absolute;overflow:hidden;left:100%;bottom:100%;-webkit-transform:translate(50%,50%);transform:translate(50%,50%);background-color:#fe4066;-webkit-box-sizing:border-box;box-sizing:border-box}.dsm-c-rp-badge .dsm-c-rp-badge-item.right{-webkit-transform:translate(8px,50%);transform:translate(8px,50%);bottom:50%}.dsm-c-rp-badge .dsm-c-rp-badge-item.right-top{-webkit-transform:translate(-50%,50%);transform:translate(-50%,50%)}.dsm-c-rp-badge .dsm-c-rp-badge-item.count{min-width:14px;padding:0 4px;height:14px;border-radius:14px;font-size:12px;color:#fff;display:-webkit-box;display:-ms-flexbox;display:flex;-webkit-box-align:center;-ms-flex-align:center;align-items:center;-webkit-box-pack:center;-ms-flex-pack:center;justify-content:center}.dsm-c-rp-badge .dsm-c-rp-badge-item.dot{border-radius:50%}.dsm-c-rp-badge .dsm-c-rp-badge-item.dot.default{width:8px;height:8px}.dsm-c-rp-badge .dsm-c-rp-badge-item.dot.mini{width:5px;height:5px}