.dsm-c-rp-icon-button{display:inline-block;-webkit-box-sizing:border-box;box-sizing:border-box;height:24px;color:#fff;border:none;background-color:transparent;outline:0;cursor:pointer;margin:0;padding:0;border-radius:2px;text-align:center}.dsm-c-rp-icon-button.noborder{background-color:transparent;border:none}.dsm-c-rp-icon-button.noborder.actived{color:#009dff}.dsm-c-rp-icon-button>.btn-text{color:#99a5be}.dsm-c-rp-icon-button>.dsm-c-rp-icon-tooltip{line-height:1}.dsm-c-rp-icon-button .dsm-c-rp-icon{pointer-events:none;opacity:1;-webkit-transition-duration:.15s;transition-duration:.15s;-webkit-transition-timing-function:ease-in;transition-timing-function:ease-in}.dsm-c-rp-icon-button:hover .font-icon i{color:#eef0f4}.dsm-c-rp-icon-button.actived,.dsm-c-rp-icon-button.actived .font-icon i,.dsm-c-rp-icon-button.actived>.btn-text{color:#009dff}.dsm-c-rp-icon-button:disabled{opacity:.3;cursor:default;pointer-events:none}