.path-editor-layer{position:absolute;left:0;right:0;top:0;bottom:0;z-index:200}.path-editor-layer canvas{width:100%;height:100%;opacity:0}.path-editor-layer>.vertical-align-line{width:0;border-left:1px solid #fe4066;position:absolute;top:0;left:0;pointer-events:none}.path-editor-layer>.horizontal-align-line{height:0;top:0;left:0;border-top:1px solid #fe4066;position:absolute;pointer-events:none}.path-editor-layer .selection{position:absolute;z-index:2;background:rgba(0,157,255,.1);border-radius:.1em;border:1px solid #009dff;will-change:top,left,bottom,right,width,height;pointer-events:none}.path-editor-layer .clip-stroke,.path-editor-layer .svg-overlay{position:absolute;top:0;left:0;overflow:visible;pointer-events:none}.path-editor-layer .clip-stroke svg,.path-editor-layer .svg-overlay svg{overflow:visible;width:100vw;height:100vh}.path-editor-layer .move-anchor{-webkit-transform-origin:0 0 0;transform-origin:0 0 0;width:4px;height:4px;border-radius:50%;border:2px solid #009dff;background:#fff}.path-editor-layer .controls-overlay,.path-editor-layer .move-anchor{position:absolute;top:0;left:0;pointer-events:none}.path-editor-layer .controls-overlay .svg-control{position:inherit;top:inherit;left:inherit;pointer-events:none}.path-editor-layer .controls-overlay .svg-control.active{z-index:3}.path-editor-layer .controls-overlay .svg-control .svg-control-line{shape-rendering:auto}.path-editor-layer .controls-overlay .svg-control .svg-control-handlein,.path-editor-layer .controls-overlay .svg-control .svg-control-handleout,.path-editor-layer .controls-overlay .svg-control .svg-control-point{position:inherit;top:inherit;left:inherit;width:4px;height:4px;border:2px solid #009dff;background:#fff;-webkit-transform-origin:0 0 0;transform-origin:0 0 0;pointer-events:none;border-radius:50%;will-change:transform}.path-editor-layer .controls-overlay .svg-control .svg-control-handlein.active,.path-editor-layer .controls-overlay .svg-control .svg-control-handleout.active,.path-editor-layer .controls-overlay .svg-control .svg-control-point.active{background:#009dff}.path-editor-layer .controls-overlay .svg-control .svg-control-line{position:inherit;top:inherit;left:inherit;stroke:#fe4066;stroke-width:1px;fill:none;pointer-events:none;overflow:visible}.path-editor-layer .set-radius{width:200px;height:50px;position:absolute;right:0;top:0;z-index:100000;background:#fff}